* {
    font-family: Arial, Helvetica, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: darkgrey;
    font-size: 1.2rem;
    width: 100%;
}

input:focus {
    outline: #167bee auto 3px;
}

.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
    width: 300px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.center {
    text-align: center;
}